},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"bloque-texto-g__texto p-4"},[_c('p',{staticClass:"mb-0"},[_vm._v("Este anlisis abre la puerta a un segundo concepto fundamental: la libertad. Para Sen, la libertad consiste en la capacidad de actuar y decidir entre distintas opciones, partiendo de condiciones iniciales similares. En este punto, se distancia de las concepciones de igualdad y justicia propuestas por John Rawls y Ronald Dworkin. Ambos autores sostienen que, en un momento inicial lo que Rawls denomina velo de la ignorancia se debe prescindir de toda subjetividad y atributos personales para garantizar igualdad. Bajo esta lgica, al ignorar las condiciones individuales, cada persona puede pensar en trminos colectivos, y as se establece una igualdad de partida que legitima las diferencias posteriores como justas.")])])
